/* Fonts */

@import url('https://fonts.googleapis.com/css?family=Nunito+Sans');
* {
    font-family: "Nunito Sans", sans-serif;
}

h1,
.h1,
.has-h-1-font-size {
    font-weight: 900;
    font-size: 2.5rem;
    color: #333333;
}

h2,
.h2,
.has-h-2-font-size {
    font-weight: bolder;
    font-size: 2rem;
    color: #333333;
}

h3,
.h3,
.has-h-3-font-size {
    font-weight: bold;
    font-size: 1.75rem;
    color: #333333;
}

h4,
.h4,
.has-h-4-font-size {
    font-weight: bold;
    font-size: 1.5rem;
    color: #333333;
}

h5,
.h5,
.has-h-5-font-size {
    font-size: 1.25rem;
    color: #333333;
}

h6,
.h6,
.has-h-6-font-size {
    font-size: 1rem;
    color: #333333;
}

p,
.p,
.has-p-font-size {
    font-weight: normal;
    font-size: 1rem;
}

strong,
.strong {
    font-weight: 900;
}

// CSS lock -- see https://fvsch.com/css-locks/
// More link
.more-link {
    display: block;
}

.a,
a {
    text-decoration: none;
    background-color: transparent;
}

.a:hover,
a:hover {
    text-decoration: underline;
    text-decoration-color: inherit;
	text-decoration-line: none !important;
}

.a:hover,
a:hover {
    span {
        text-decoration: underline;
        text-decoration-color: inherit;
    }
}

a[rel~="tag"]:before {
    content: "#";
}

p.has-text-color a:hover {
    color: inherit !important;
    text-decoration: underline;
    text-decoration-color: inherit;
}

h1.has-text-color,
h2.has-text-color,
h3.has-text-color,
h4.has-text-color,
h5.has-text-color,
h6.has-text-color {
    a,
    .a {
        color: inherit !important;
    }
    .a:hover,
    a:hover {
        text-decoration: underline;
        text-decoration-color: inherit;
    }
}

main .a:hover,
main a:hover,
footer .a:hover,
footer a:hover {
    //color: inherit !important;
}

h1,
.h1,
h2,
.h2,
h3,
.h3,
h4,
.h4,
h5,
.h5,
h6,
.h6 {
    &.alignwide,
    &.alignfull {
        margin-top: 0 !important;
        margin-bottom: 0.5rem !important;
    }
}

